import { Injectable } from '@angular/core';

@Injectable()
export class StockService {
  public stocks: Stock[];
  constructor() {
	  this.stocks = [
		  new Stock(1, "", 1.00, 3.1, "", ["SMM", ""]),
		  new Stock(2, "", 4.00, 2.1, "", ["", ""]),
		  new Stock(3, "", 7.00, 4.1, "", ["", ""]),
		  new Stock(4, "", 20.00, 5.1, "", ["", ""]),
		  new Stock(5, "", 12.00, 1.1, "", ["", ""]),
		  new Stock(6, "", 3.00, 0.1, "", ["", ""]),
		  new Stock(7, "", 6.00, 3.1, "", ["", ""]),
		  new Stock(8, "", 2.00, 4.1, "", ["", ""]),
	  ];
  }

  getStocks(): Stock[] {
  	return this.stocks;
  }

  getStock(id: number): Stock {
  	// TODO ??? find
	let stock = this.stocks.find(stock => stock.id == id)
	if (!stock) {
		return new Stock(0, "", 0, 0, "", []);
	}
	return stock;
  }
}


export class Stock {
	constructor(
		public id: number,
		public name: string,
		public price: number,
		public rating: number, // 
		public desc: string,
		public categories: Array<string>
	) {}
}



